* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
}

.weather--container {
  width: 432px;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 25px;
  gap: 25px;

  margin: 0 auto;
  padding: 60px 40px 40px 40px;

  -webkit-align-items: center;

          align-items: center;

  background-color: PaleTurquoise;
  background-image: url("https://images.pexels.com/photos/1544880/pexels-photo-1544880.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260");
  background-size: cover;
}

form {
  display: -webkit-flex;
  display: flex;
  grid-gap: 12px;
  gap: 12px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

input {
  width: 275px;
  height: 43px;
  padding: 0 12px;
  font-size: 18px;
  background-color: bisque;
  border: 2px solid transparent;
  background: #fff;
  box-shadow: 3px 4px 20px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  outline: none;
}

input:focus {
  border: 2px solid SteelBlue;
}

button.show--result__btn {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  position: relative;
  width: 40%;
  height: 43px;

  font-size: 18px;

  color: white;
  background: SteelBlue;
  border: 0;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.3s;
  box-shadow: 3px 4px 20px rgba(0, 0, 0, 0.25);
}

button.show--result__btn:hover {
  background: LightSteelBlue;
}

button.show--result:active {
  background: LightSteelBlue;
  box-shadow: 3px 4px 20px rgba(0, 0, 0, 0);

}

.weather--result {
  color: #1C1C1C;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 12px;
  gap: 12px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.weather--result__city {
  font-weight: 500;
  font-size: 50px;
  line-height: 59px;
  text-align: center;
}

.weather--result__data {
  font-weight: normal;
  font-size: 25px;
  line-height: 30px;
}

.weather--result__temperature {
  font-weight: bold;
  font-size: 75px;
  line-height: 89px;
  border-bottom: 3px dashed SteelBlue;
}

.weather--favourites {
  color: white;
  text-align: center;
  width: 320px;
  height: 150px;
  font-weight: 500;
  font-size: 25px;
  line-height: 30px;
  overflow-y: auto;
  overflow-x: hidden;
}

ul {
  list-style: none;
  cursor: pointer;
}
